.pagination-fixed{
    position: fixed;
    list-style: none;
    top:50%;
    left: 0%;
    transform: translate(-50%,-50%);
}
.pagination-item{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left:1rem ;
    color:white;
}